.sidebar-wrapper{min-height:60vh;background:#fff;box-shadow:0 1px 20px rgba(0,0,0,.1);border-radius:10px}.c-side-user{background-color:#d8dbe0;padding:10px 0;border-radius:6px}.c-side-user .avatar-upload{position:relative;max-width:205px;margin:10px auto}.c-side-user .avatar-upload .avatar-edit{position:absolute;right:1px;z-index:1;border:10px;height:35px !important;width:35px !important;bottom:-2px}.c-side-user .avatar-upload .avatar-edit input{display:none}.c-side-user .avatar-upload .avatar-edit input+label{display:inline-block;width:34px;height:34px;margin-bottom:0;border-radius:100%;background:#fff;border:1px solid transparent;box-shadow:0px 2px 4px 0px rgba(0,0,0,.12);cursor:pointer;font-weight:normal;transition:all .2s ease-in-out;position:relative}.c-side-user .avatar-upload .avatar-edit input+label:hover{background:#f1f1f1;border-color:#d6d6d6}.c-side-user .avatar-upload .avatar-edit input+label svg{color:#757575;position:absolute;top:8px;left:6px;right:0;text-align:center;margin:auto;font-size:16px}.c-side-user .avatar-upload .avatar-preview{width:120px;height:120px;position:relative;border-radius:100%;border:6px solid #f8f8f8;box-shadow:0px 2px 4px 0px rgba(0,0,0,.1);left:50%;top:50%;-webkit-transform:translate(-50%, 0%);transform:translate(-50%, 0%)}.c-side-user .avatar-upload .avatar-preview>div{width:100%;height:100%;border-radius:100%;background-size:cover;background-repeat:no-repeat;background-position:center}.sidebar-menu{margin-top:15px}.sidebar-menu .sidebar-menu-item .sidebar-menu-link{margin:4px 0;color:#474646;padding:10px 22px;font-weight:600;font-size:15px;text-decoration:none;display:block;border-radius:.25rem}.sidebar-menu .sidebar-menu-item .sidebar-menu-link .icon{font-size:21px;margin-right:15px;margin-top:-3px}.sidebar-menu .sidebar-menu-item .active{background:#f1971f;color:#fff}
.dashboard-wrapper{min-height:60vh;background:#fff;box-shadow:0 1px 20px rgba(0,0,0,.1);border-radius:10px}
